body {
  margin: 0;
  padding: 0;
  font-family: "Lato";
  height: 100%;
  color: white;
}

html, #root {
  height: 100%;
}

.logo {
    -webkit-transform: translateY(60px);
            transform: translateY(60px);
    margin: 10px;
    width: 150px;
    -webkit-align-self: center;
            align-self: center;
}

.header > h1 {
    margin: 0;
}

@media only screen and (max-width: 1087px) {
    .logo {
        width: 100px;
        -webkit-transform: translateY(40px);
                transform: translateY(40px);
    }

    .header > h1 {
        font-size: 1.5em;
    }
}

.button-stop {
    padding: 17px 30px 16px 30px;
    border-radius: 30px;
    font-size: 1.5em;
    margin-top: 130px;    
    background-color: white;
    border: 1px solid transparent;
}

.button-stop:focus {
    outline: none;
}

.button-stop:hover {
    cursor: pointer;
    opacity: .7;
}

.button-icon {
    height: 50px;
}

.details-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    margin-top: 40px;
    -webkit-align-items: center;
            align-items: center;
}

.detail-element {
    font-size: 2em;
    width: 100%;
}

.maps-icon {
    width: 100px;
    height: 100px;
}

.play-again {
    margin-top: 40px;
    font-size: 1.5em;
}

.play-again:hover {
    cursor: pointer;
    opacity: .7;
}

@media only screen and (max-width: 400px) {
    .detail-element {
        font-size: 1.5em;
    }

    .maps-icon { 
        width: 60px;
        height: 60px;
    }
}

.restaurant-name {
    font-size: 2em;
    font-weight: lighter;
}

.slider {
    border-bottom: 1px solid black;
    margin-left: 60px;
    margin-right: 60px;    
}

.arrow-up {
    position: absolute;
    left: 48.5%;
    width: 0; 
    height: 0; 
    border-left: 25px solid transparent;
    border-right: 25px solid transparent;
    border-bottom: 25px solid black;
}

@media only screen and (max-width: 400px) {
    .slider {
        margin-left: 20px;
        margin-right: 20px;
    }

    .arrow-up {
        left: 44%;
    }

    .restaurant-name {
        font-size: 2em;
    }
}

.spinner {
    width: 40px;
    height: 40px;
    margin: 100px auto;
    background-color: #fff;
  
    border-radius: 100%;  
    -webkit-animation: sk-scaleout 1.0s infinite ease-in-out;
    animation: sk-scaleout 1.0s infinite ease-in-out;
  }
  
  @-webkit-keyframes sk-scaleout {
    0% { -webkit-transform: scale(0) }
    100% {
      -webkit-transform: scale(1.0);
      opacity: 0;
    }
  }
  
  @keyframes sk-scaleout {
    0% { 
      -webkit-transform: scale(0);
      transform: scale(0);
    } 100% {
      -webkit-transform: scale(1.0);
      transform: scale(1.0);
      opacity: 0;
    }
  }

.footer-wrapper > h3 {
    font-weight: lighter;
    font-size: 0.75em;
}

.footer-link {
    text-decoration: none;    
    color: black;
    font-size: 1.2em;
}

.footer-link:hover {
    opacity: .7;
}

.App {
  background-color: #b51432;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: center;  
}

